#top-header {
    background: #014634;
    height: 60px;
    line-height: 60px;
    margin-bottom: 8px;
}

#top-header h1 {
    margin: 0;
    color: #fff;
    padding-left: 12px;
    font-weight: normal;
    font-size: 28px;
    float: left;
}

#top-header img {
    display: none;
    width: 50px;
    height: 50px;
    margin: 4px 0 0 8px;
}

#top-header p {
    display: inline;
    color: #fff;
    padding-right: 8px;
}

#top-header-auth-button {
    float: right;
    margin-right: 12px;
}

@media only screen and (max-width: 609px) {
    #top-header h1 {
        display: none;
    }

    #top-header img {
        display: inline;
    }
}

.weekview-table {
    border: 1px solid #cccccc;
    border-collapse: separate;
    border-spacing: 0;
    width: 55%;
    height: 100%;
    table-layout: fixed;
}

.weekview-table td span {
    display:block;
    position: relative;
    height: 100%;
}

.weekview-table td div {
    overflow: hidden;
    /* text-overflow: ellipsis;
    white-space: nowrap;
    height: 100%;*/
    position: absolute;
    width: 100%;
    top: 0;
}

.weekview-table tr {
    /* height: 16px; */ /* 8px; */
}

.weekview-table tr td:first-child {
    /* width: 50px; */
    border-width: 1px 1px 0 0;
}

.weekview-table tbody tr:first-child td:first-child {
    border-top: 0;
}

.weekview-table tr th:first-child {
    /* width: 50px; */
    border-width: 0 1px 1px 0;
}

.weekview-table thead th:nth-child(2) {
    border-left: 0;
}

.weekview-cell-label {
    border: 1px solid #cccccc;
    background-color: #fff;
}

.weekview-cell-hidden {
    display: none;
}

.weekview-table th:not(:first-child) {
    /* width: 100px; */
    border-width: 0 0 1px 1px;
}

.weekview-table td:not(:first-child) {
    /* width: 101px; */
    /* border-width: 0 0 1px 1px; */
}

.weekview-table tr:nth-child(8n-7),
.weekview-table tbody tr:nth-child(8n-6),
.weekview-table tbody tr:nth-child(8n-5),
.weekview-table tbody tr:nth-child(8n-4) {
    background-color: #f5f5f5;
}

.weekview-table tbody tr:nth-child(8n-3),
.weekview-table tbody tr:nth-child(8n-2),
.weekview-table tbody tr:nth-child(8n-1),
.weekview-table tbody tr:nth-child(8n) {
    background-color: #e5e5e5;
}

.weekview-title {
    background-color: #014634 !important;
    color: #fff !important;
}
.weekview-title td {
    padding: 4px;
}

.card {
    background: #fff;
    border-radius: 2px;
    /* display: inline-block; */
    height: 300px;
    margin-left: 12px;
    position: relative;
    width: 300px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    vertical-align: bottom;
    display: flex;
    flex-flow: column;
    /* TODO: Fix overflow */
}

/* .card-content {
    padding: 12px;
} */

.card-header {
    color: #fff;
    background-color: #014634;
    padding: 4px;
    flex: 0 1 auto;
}

.card-content {
    flex: 1 1 auto;
    height: 100%;
}

.card ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

#group-page-container {
    margin: 8px;
}

#group-my-schedule-card .card-content {
    padding: 12px;
}

#group-my-schedule-card .weekview-table {
    height: 600px;
}

#group-schedules {
    padding-top: 12px;
}

#group-schedules .weekview-table {
    width: 100%;
    height: 600px;
}
.course-container {
    /* margin-left: 8px; */
}

#course-container-buttons {
    margin-top: 4px;
    position: relative;
}

#course-container-buttons button {
    margin-right: 4px;
    border: 1px solid #cccccc;
    border-radius: 4px;
    background-color: #fff;
    color: #333333;
}

#course-container-buttons img {
    height: 23px;
    width: 23px;
    position: absolute;
    top: 3px;
}

.course-select {
    display: inline-block;
    width: 200px;
    padding-right: 8px;
    padding-bottom: 4px;
}

.course-select:last-of-type {
    width: 300px;
}

.course-select:last-of-type span {
    /* margin-top: -24px; */
    float: right;
}

.course-select:last-of-type span.multiple {
    margin-top: -7px;
    font-size: 14px;
}


#landing-container {
    position: absolute;
    top: 60px;
    bottom: 0;
    width: 100%;
    padding: 8px 8px 16px 8px;
    box-sizing: border-box;

    display: grid;
    grid-gap: 12px;
    grid-template-columns: 1fr 5fr;
}

.card {
    height: 100%;
    width: 100%;
    margin-left: 0;
}

#create-group-card {
    grid-column: 1;
    grid-row: 1;
}

#create-group-card .card-content {
    padding: 12px;
}

#my-groups-card {
    grid-column: 1;
    grid-row: 2;
}

#my-groups-card .card-content {
    padding-top: 12px;
}

#my-groups-card li:hover {
    background-color: #e2e3e3;
}

#my-groups-card a {
    font-size: 16px;
    display: block;
    padding: 4px 12px 4px 12px;
    text-decoration: none;
}

#my-schedules-card {
    grid-column: 2 / 6;
    grid-row: 1 / 3;
}

#create-group-button {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 12px;
}

#name-label > p {
    margin: 0;
    display: inline;
    margin-right: 8px;
    font-weight: bold;
}

#create-group-card label > input {
    margin-bottom: 8px;
}

#checkbox-label  > p {
    margin: 0;
    display: inline;
    font-size: 14px;
}

#my-schedules-card .card-content > div {
    height: 100%;
}
body {
    margin: 0;
    font-family: sans-serif;
    background-color: #e2e3e3;
}

html, body, #root, #app {
    height: 100%;
}

.button {
    border: 1px solid #e5e5e5;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 6px;
    padding: 5px 8px;
    font-size: 13px;
}

.button.large {
    padding: 5px 8px;
    font-size: 15px;
}

.button.green {
    background-color: #014634 !important;
    color: white !important;
}

.button.gray {
    background-color: #eff0f1;
    color: black;
}

